<template>
  <div class="p-6 max-w-4xl mx-auto">
    <!-- Gradient Header -->
    <div class="text-center mb-8">
      <h2 class="text-3xl font-bold bg-gradient-to-r from-blue-600 to-indigo-600 text-white py-4 rounded-lg shadow">
        📅 Upcoming Group Sessions
      </h2>
    </div>

    <!-- Display sessions -->
    <div v-if="sessions.length" class="grid grid-cols-1 md:grid-cols-2 gap-6 mb-10">
      <div
        v-for="session in sessions"
        :key="session.id"
        class="bg-white border border-gray-200 p-5 rounded-xl shadow-md hover:shadow-lg transition duration-200"
      >
        <h4 class="text-xl font-semibold text-blue-800 mb-1 flex items-center gap-2">
          🧠 {{ session.topic }}
        </h4>
        <p class="text-gray-600 text-sm mb-3">🕒 {{ formatTime(session.time) }}</p>
        <button
          class="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 shadow-sm hover:shadow-md transition"
          @click="joinSession(session)"
        >
          Join Session
        </button>
      </div>
    </div>

    <!-- Session Creation Form -->
    <div class="bg-gradient-to-br from-white to-blue-50 rounded-xl shadow-lg p-8 border border-gray-200">
      <h3 class="text-2xl font-bold mb-6 text-blue-700">➕ Create New Session</h3>
      <form @submit.prevent="createSession" class="space-y-5">
        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1">Session Topic</label>
          <input
            v-model="topic"
            placeholder="e.g., Doubt Clearing on Signal Processing"
            required
            class="w-full px-4 py-2 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
        </div>

        <div>
          <label class="block text-sm font-medium text-gray-700 mb-1">Date & Time</label>
          <input
            v-model="time"
            type="datetime-local"
            required
            class="w-full px-4 py-2 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
        </div>

        <button
          type="submit"
          class="w-full bg-blue-600 text-white py-2 px-4 rounded-lg font-semibold hover:bg-blue-700 shadow hover:shadow-md transition"
        >
          ✅ Create Session
        </button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      topic: '',
      time: '',
      sessions: []
    };
  },
  mounted() {
    this.fetchSessions();
  },
  methods: {
    formatTime(datetimeStr) {
      return new Date(datetimeStr).toLocaleString();
    },

    async fetchSessions() {
      try {
        const token = localStorage.getItem('token');
        const res = await fetch('/session/all', {
          headers: {
            Authorization: `Bearer ${token}`
          }
        });

        if (!res.ok) throw new Error('Failed to fetch sessions');
        this.sessions = await res.json();
      } catch (err) {
        console.error('❌ Fetch Error:', err.message);
      }
    },

    async createSession() {
      try {
        const token = localStorage.getItem('token');
        if (!token) {
          alert('❌ You must be logged in to create a session');
          return;
        }

        const response = await fetch('/session/create', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${token}`
          },
          body: JSON.stringify({
            topic: this.topic,
            time: this.time
          })
        });

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || 'Unknown error');
        }

        const result = await response.json();
        alert(`✅ Session created: "${result.topic}" at ${this.formatTime(result.time)}`);

        this.topic = '';
        this.time = '';
        this.fetchSessions(); // Refresh sessions list
      } catch (err) {
        alert(`❌ Error: ${err.message}`);
        console.error(err);
      }
    },

    joinSession(session) {
      alert(`🚀 Joining "${session.topic}" at ${this.formatTime(session.time)} 🎥`);
      // Later: Integrate with Zoom, Jitsi, or custom WebRTC room
    }
  }
};
</script>

<style scoped>
/* You can optionally use dark mode or add more transitions here */
</style>
